.section-iconbox-2 {
    .icon {
        max-width: 80px;
        margin: 0 auto 15px;   
        @media (min-width: 750px) {
            margin-bottom: 22px;
        }    
    } 
    .section-header {
        max-width: none;
        margin: 0;
    }
    .content {
        .heading {
            font-size: var(--body-font-size);
            margin-bottom: 2px;
        }
        .subtext {
            margin: 0;
            color: var(--body-color-2); 
        } 
    } 
}